body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.airq-display{
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto;
  width: 80%;
  height: 280px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: stretch;
          align-items: stretch; 
}
.text-dis{
  -webkit-flex-grow: 4;
          flex-grow: 4;
  background-image: linear-gradient(to right, rgba(107, 245, 255, 0.87),rgb(107, 245, 255,0));
}
.img-dis{
  -webkit-flex-grow: 6;
          flex-grow: 6;
  background-image:url(../../static/media/tianjin.6041738b.jpg);
  background-size:cover;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.input-Form{
    display: -webkit-flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding: 10px,5px;
    background-image: linear-gradient(to bottom, coral,rgb(255, 255, 255))
}
.input-Form> div{
    -webkit-flex:auto;
            flex:auto;
    -webkit-align-content: center;
            align-content: center;
    text-align: center;
}
.picker{
    font-size: 20px;
}
select{
    font-size:20px;
}
button{
    font-size:20px;
    border-bottom-color: tomato;
    box-shadow: 1px;
    border-radius: 5px;
}


.AQ{
    display: flexbox;
    font-size: 15px;
    color:red;
    -webkit-flex-direction: row;
            flex-direction: row;
    float: center;
    text-align: center;

}
.AQ:nth-child(1){
    -webkit-flex:auto;
            flex:auto;
}
.AQ:nth-child(2){
    -webkit-flex:auto;
            flex:auto;
}
.AQ:nth-child(3){
    -webkit-flex:auto;
            flex:auto;
}

.Title{
    margin: auto;
    font-size: 50px;
    color: #61dafb;
    text-align: center;
    -webkit-animation: slide-in 3s forwards;
            animation: slide-in 3s forwards;
    background-color: coral;
}

@-webkit-keyframes slide-in {
    from {
        margin-left: -30%;
      }
      to {
        margin-left: 0;
      }
}

@keyframes slide-in {
    from {
        margin-left: -30%;
      }
      to {
        margin-left: 0;
      }
}
